import React, { Component } from 'react';
import {Section, SectionTitle} from './Section';
import LinearProgress from 'material-ui/LinearProgress';
import SkillsIcon from 'material-ui/svg-icons/action/build';
import Content from '../Content';
import './Skills.css'

class Skill extends Component {
  render() {
    return (
      <div className="skill row">
        <div className="col-md-4 col-sm-5 col-xs-12">
          {this.props.label}
          { this.props.description ? (
            <span className="description">({this.props.description})</span>
          ) : null}
        </div>
        <div className="col-md-8 col-sm-7 col-xs-12">
          <LinearProgress mode="determinate" value={this.props.value} style={{ marginTop: '7px' }} />
        </div>
      </div>
    );
  }
}

class Skills extends Component {
  render() {
    return (
      <Section id="skills">
        <SectionTitle title="Compétences" rightIcon={<SkillsIcon/>}/>
          <div className="skills">
            {Content.skills.map((skillSection, i) =>
              <div key={i}>
                <h3>{skillSection.title}</h3>
                {skillSection.skills.map((skill, i) =>
                  <Skill key={i} label={skill.label} description={skill.description} value={skill.value}/>
                )}
              </div>
            )}
          </div>
      </Section>
    );
  }
}

export default Skills;



