import React, { Component } from 'react';
import {Timeline, TimelineBlock, TimelineTitle} from './Timeline';
import {Section, SectionTitle} from './Section';
import Chip from 'material-ui/Chip';
import Content from '../Content';
import WorkExperienceIcon from 'material-ui/svg-icons/places/business-center';

const styles = {
  chip: {
    margin: 2
  },
  wrapper: {
    display: 'flex',
    flexWrap: 'wrap',
  },
};

class WorkExperience extends Component {
  render() {
    return (
      <Section id="work-experience">
        <SectionTitle title="Expérience professionnelle" rightIcon={<WorkExperienceIcon/>}/>
        <Timeline>
          {Content.experiences.map((experienceSection, i) =>
            <div key={i}>
              <TimelineTitle>{experienceSection.title}</TimelineTitle>
              {experienceSection.experiences.map((experience, i) =>
                <TimelineBlock key={i}>
                  <div><strong>{experience.date}</strong></div>
                  <div>{experience.title}</div>
                  <div style={styles.wrapper}>
                    {experience.skills.map((skill, i) =>
                        <Chip key={i} style={styles.chip} backgroundColor={Content.skillColors[skill]}>{skill}</Chip>
                    )}
                  </div>
                </TimelineBlock>
              )}
            </div>
          )}
        </Timeline>
      </Section>
    );
  }
}

export default WorkExperience;



