import React, { Component } from 'react';
import {Section, SectionTitle} from './Section';
import {Card, CardMedia, CardTitle} from 'material-ui/Card';
import Content from '../Content';
import HobbiesIcon from 'material-ui/svg-icons/action/favorite';

class Hobby extends Component {
  render() {
    return (
      <Card>
        <CardMedia
          overlay={<CardTitle title={this.props.title} subtitle={this.props.subtitle} />}
        >
          <img src={this.props.imgSrc} role="presentation" />
        </CardMedia>
      </Card>
    );
  }
}

class Hobbies extends Component {
  render() {
    return (
      <Section id="hobbies">
        <SectionTitle title="Centres d'intérêt" rightIcon={<HobbiesIcon/>}/>
        <div className="row">
          {Content.hobbies.map((hobby, i) =>
            <div key={i} className="col-sm-4 col-xs-12">
              <Hobby title={hobby.title} subtitle={hobby.subtitle} imgSrc={hobby.imgSrc}/>
            </div>
          )}
        </div>
      </Section>
    );
  }
}

export default Hobbies;



