import React, { Component } from 'react';

import AppBar from 'material-ui/AppBar';
import MdDrawer from 'material-ui/Drawer';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';
import Content from '../Content'

import ProfilIcon from 'material-ui/svg-icons/social/person';
import WorkExperienceIcon from 'material-ui/svg-icons/places/business-center';
import SkillsIcon from 'material-ui/svg-icons/action/build';
import EducationIcon from 'material-ui/svg-icons/social/school';
import HobbiesIcon from 'material-ui/svg-icons/action/favorite';
import ContactIcon from 'material-ui/svg-icons/content/mail';

class Drawer extends Component {
  render() {
    return (
      <MdDrawer
        docked={false}
        open={this.props.open}
        onRequestChange={(open) => (open)?this.props.showDrawer():this.props.hideDrawer()}
        width={300}
      >
        <AppBar
          title={Content.title}
          onLeftIconButtonTouchTap={this.props.hideDrawer}
        />
        <Menu>
          <MenuItem onTouchTap={() => window.location.href = '#profil' }  leftIcon={<ProfilIcon />}>Profile</MenuItem>
          <MenuItem onTouchTap={() => window.location.href = '#work-experience' } leftIcon={<WorkExperienceIcon />}>Expérience professionnelle</MenuItem>
          <MenuItem onTouchTap={() => window.location.href = '#skills' } leftIcon={<SkillsIcon />}>Compétences</MenuItem>
          <MenuItem onTouchTap={() => window.location.href = '#education' } leftIcon={<EducationIcon />}>Formation</MenuItem>
          <MenuItem onTouchTap={() => window.location.href = '#hobbies' } leftIcon={<HobbiesIcon />}>Centres d''intérêt</MenuItem>
          <Divider />
          <MenuItem onTouchTap={() => window.location.href = '#contact' } leftIcon={<ContactIcon />}>Contact</MenuItem>
        </Menu>
      </MdDrawer>
    );
  }
}

export default Drawer;



