import React, { Component } from 'react';
import {Section, SectionTitle} from './Section';
import Content from '../Content';
import IconButton from 'material-ui/IconButton';
import ContactIcon from 'material-ui/svg-icons/content/mail';

const style = {
  button: {
    height: 64,
    width: 64,
  },
  icon: {
    fontSize: 48,
  }
}

class Contact extends Component {
  render() {
    return (
      <Section id="contact">
        <SectionTitle title="Contact" rightIcon={<ContactIcon/>}/>
        <div className="row center-xs">
          <div className="col-sm-4 col-xs-12">
            <IconButton iconClassName="fa fa-envelope" style={style.button} iconStyle={style.icon} href={'mailto:' + Content.contacts.mail} />
            <div>Contactez moi par mail</div>
          </div>
          <div className="col-sm-4 col-xs-12">
            <IconButton iconClassName="fa fa-github" style={style.button} iconStyle={style.icon} href={Content.contacts.github} />
            <div>Découvrez mon GitHub</div>
          </div>
          <div className="col-sm-4 col-xs-12">
            <IconButton iconClassName="fa fa-twitter" style={style.button} iconStyle={style.icon} href={Content.contacts.twitter} />
            <div>Suivez moi sur Twitter</div>
          </div>
        </div>
      </Section>
    );
  }
}

export default Contact;



