import React, { Component } from 'react';
import muiThemeable from 'material-ui/styles/muiThemeable';
import Content from '../Content';
import './Footer.css'

class Footer extends Component {
  render() {
    return (
      <div className="footer row center-xs middle-xs" style={{backgroundColor: this.props.muiTheme.palette.primary1Color}}>
        <div>{Content.footer}</div>
      </div>
    );
  }
}

var FooterThemeable = muiThemeable()(Footer);

export default FooterThemeable;



