import React, { Component } from 'react';
import muiThemeable from 'material-ui/styles/muiThemeable';
import "./Timeline.css"

class Timeline extends Component {
  render() {
    return (
      <div className="timeline">
        {this.props.children}
      </div>
    );
  }
}

class TimelineBlock extends Component {
  render() {
    return (
      <div className="timeline-block">
        <svg className="line" width="20" xmlns="http://www.w3.org/2000/svg" version="1.1">
          <rect x="9" y="0" width="2" height="100%" style={{fill: this.props.muiTheme.palette.borderColor}} />
          <ellipse cx="10" cy="10" rx="10" ry="10" style={{fill: this.props.muiTheme.palette.primary1Color}} />
        </svg>
        <div className="timeline-block-content">
              {this.props.children}
        </div>
     </div>
    );
  }
}

class TimelineTitle extends Component {
  render() {
    return (
      <div className="timeline-title">
        <svg className="line" width="20" xmlns="http://www.w3.org/2000/svg" version="1.1">
          <rect x="9" y="0" width="2" height="100%" style={{fill: this.props.muiTheme.palette.borderColor}} />
        </svg>
        <h2 style={{color: this.props.muiTheme.palette.primary1Color}}>{this.props.children}</h2>
      </div>
    );
  }
}

class TimelineSeparator extends Component {
  render() {
    return (
      <div className="separator">
        <svg className="line" width="20" xmlns="http://www.w3.org/2000/svg" version="1.1">
          <rect x="9" y="0" width="2" height="100%" style={{fill: this.props.muiTheme.palette.borderColor}} />
        </svg>
      </div>
    );
  }
}

var TimelineBlockThemeable = muiThemeable()(TimelineBlock);
var TimelineTitleThemeable = muiThemeable()(TimelineTitle);
var TimelineSeparatorThemeable = muiThemeable()(TimelineSeparator);

export { Timeline, TimelineBlockThemeable as TimelineBlock, TimelineTitleThemeable as TimelineTitle, TimelineSeparatorThemeable as TimelineSeparator };



