import React, { Component } from 'react';
import {Timeline, TimelineBlock, TimelineSeparator} from './Timeline';
import {Section, SectionTitle} from './Section';
import Content from '../Content';
import EducationIcon from 'material-ui/svg-icons/social/school';

class Education extends Component {
  render() {
    return (
      <Section id="education">
        <SectionTitle title="Formation" rightIcon={<EducationIcon/>}/>
        <Timeline>
          <TimelineSeparator/>
          {Content.educations.map((education, i) =>
            <TimelineBlock key={i}>
              <div><strong>{education.date}</strong> - {education.title}</div>
            </TimelineBlock>
          )}
        </Timeline>
      </Section>
    );
  }
}

export default Education;



