import { SHOW_DRAWER, HIDE_DRAWER, TOGGLE_DRAWER } from '../actions/drawer';

const drawer = (state = {open: false}, action) => {
  switch (action.type) {
    case SHOW_DRAWER:
      return {open: true};

    case HIDE_DRAWER:
      return {open: false};

    case TOGGLE_DRAWER:
      return {open: !state.open};

    default:
      return state;
  }
};

export default drawer;



